import React, {useState} from 'react';
import st from './style.module.scss'

function Card(props) {
    const {id, english, transcription, russian} = props;
    const [pressed, setPressed] = useState (false)

 const handleCheck = () => {
    setPressed(!pressed)
 }
    
    const translation = (
        <div className={st.translation}>{russian}</div>
    )
    const checkBtn = (
        <div className={st.btn} onClick={handleCheck}>Проверить</div>
    )

    return ( 
        <div className={st.card} id={id}>
            <div className={st.english_word}>{english}</div>
            <div className={st.transcript}>{transcription}</div>
            <>{pressed ? translation : checkBtn}</>
        </div> 
    );
}

export default Card;